<template>
	<div>
    <div class="block">
      <div>
        <el-table
          ref="filterTable"
          :data="tableData"
          stripe
          style="width: 100%"
          height="750">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label=": ">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.org }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perName }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perTel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.perAddr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.addr }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.code }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.contName }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.contTel }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.status }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.start }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.end }}</span>
                </el-form-item>
                <el-form-item label=": ">
                  <span>{{ props.row.pick }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <!--  -->
          <el-table-column
            type="index"
            :index="indexMethod"
            width="50">
          </el-table-column>
          <el-table-column
            type="selection"
            width="70">
          </el-table-column>
          <el-table-column
            label=""
            prop="id"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="org"
            width="150"
            :filters="[{ text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: '', value: '' }
                      , { text: 'EMS', value: 'EMS' }]"
            :filter-method="filterOrg"
            filter-placement="bottom-end">
            <template slot-scope="scope">
              <el-tag
                :type="scope.row.org === '' ? 'primary' : 'success'"
                disable-transitions>{{scope.row.org}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
            label=""
            prop="perName"
            width="150">
          </el-table-column>
          <el-table-column
            label=""
            prop="code"
            width="150">
          </el-table-column>
          <el-table-column
            label=""
            prop="start"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="end"
            width="200">
          </el-table-column>
          <el-table-column
            label=""
            prop="pick"
            width="100">
          </el-table-column>
          <el-table-column
            align="right"
            width="300">
            <template slot="header" slot-scope="scope">
              <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          :hide-on-single-page="value"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
	import Constant from "../pub/Constant"
  export default {
		name: "UserIsPack",
    data() {
      return {
        baseUrl: Constant.data.baseUrl,
        currentPage: 1,       // 
        total: 21,            // 
        pageSize: 10,         // 
        search: '',
        tableData: [{
          id: '12987122',
          org: '',
          per_name: 'user1',
          perTel: '12345678900',
          perAddr: '',
          addr: '',
          code: '1-1-16',
          contName: '',
          contTel: '12345678910',
          status: '',
          start: '2020-12-28 10:24:00',
          end: '',
          pick: ''
        }],
      }
    },
    methods: {
		  // 
      handleSizeChange(val) {
        console.log(` ${val} `)
      },
      // 
      handleCurrentChange(val) {
        console.log(`: ${val}`)
        this.getPacks()
      },
      // 
      handleDelete(index, row) {
        console.log(index, row)
        const _this = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let param = new URLSearchParams()
          param.append('id', _this.tableData[index].id)
          let token = localStorage.getItem('token')
          param.append('token', token)
          _this.$axios({
            method: 'post',
            url: _this.baseUrl + '/pack/deletePack',
            data: param
          })
            .then(function (response) {
              console.log(response.data)
              if (response.data === 'please login to operate') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
                _this.$router.push('/loginAndRegister')
              } else if (response.data === 'do fail') {
                _this.$notify({
                  showClose: true,
                  title: '',
                  message: '',
                  type: 'warning'
                })
              } else if (response.data === 'do success') {
                _this.$message({
                  showClose: true,
                  type: 'success',
                  message: '!'
                })
                console.log("11111111111")
                let NewPage = "_empty" + "?time=" + new Date().getTime() / 500
                console.log(NewPage)
                _this.$router.push(NewPage)
                _this.$router.go(-1)
              }
            })
            .catch(function (error) {
              console.log(error)
              _this.$notify.error({
                showClose: true,
                title: '',
                message: ''
              })
            })
        }).catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: ''
          })
        })
      },
      // 
      filterOrg(value, row) {
        return row.org === value
      },
      getPacks() {
        let param = new URLSearchParams()
        let token = localStorage.getItem("token")
        param.append('currentPage', this.currentPage)
        param.append('pageSize', this.pageSize)
        param.append('token', token)
        const _this = this
        console.log("")
        this.$axios({
          method: 'post',
          url: _this.baseUrl + '/pack/getUserIsPick/' + _this.currentPage,
          data: param
        })
          .then(function (response) {
            console.log("")
            console.log(response.data)
            if (response.data.fail === 'get info fail') {
              _this.$notify({
                showClose: true,
                title: '',
                message: '',
                type: 'warning'
              })
              _this.$router.push('/loginAndRegister')
            } else {
              _this.total = response.data.pack_result.total
              _this.tableData = response.data.pack_result.list
            }
          })
          .catch(function (error) {
            console.log(error)
            _this.$notify.error({
              showClose: true,
              title: '',
              message: ''
            })
          })
      },
      indexMethod(index) {
        return (this.currentPage - 1) * this.pageSize + index + 1
      }
    },
    created() {
      this.getPacks()
    },
    mounted() {
		  this.getPacks()
    }
  }
</script>

<style scoped>
  .demo-table-expand {
    font-size: 0;
    font-weight: bold;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



